import whenDomReady from 'when-dom-ready';
import Config from '../config';
import Sync from '../models/sync';

const PENDING  = Symbol('PENDING');
const ERROR    = Symbol('ERROR');
const COMPLETE = Symbol('COMPLETE');

export default class Bootstrap {

    constructor(options = {}) {
        this.options = options;
        this.callbacks = { onReady: [] };
        this.status = PENDING;
        if (this.options.onReady) { this.onReady(this.options.onReady); }

        whenDomReady(() => this.readLoadUrl());
    }

    tagMatches(tag) {
        return !!tag.src.match(this.options.srcTag);
    }

    readLoadUrl() {
        const tags = document.querySelectorAll('script');
        for (let i = 0; i < tags.length; i += 1) {
            const tag = tags[i];
            if (this.tagMatches(tag)) {
                this.tagData = tag.dataset;
                this.setApiHost(tag.src.replace(this.options.srcTag, ''));
                return;
            }
        }
        this.state = ERROR;
        console.error('Unable to find script tag that Stockor was loaded from'); // eslint-disable-line no-console
    }

    setApiHost(host) {
        Config.api_host = host;
        if (this.options.bootstrap) {
            this.fetchConfig();
        } else {
            this.signalDone();
        }
    }

    onReady(cb) {
        return this.callbacks.onReady.push(cb);
    }

    fetchConfig() {
        Sync.perform(`${Config.api_path}/lanes/bootstrap`).then(() => this.signalDone());
    }

    signalDone() {
        this.status = COMPLETE;
        this.callbacks.onReady.map(cb => cb(this));

    }
}


